
import { defineComponent, toRefs, computed, onMounted } from "vue";
import { useStore } from "vuex";

import FabricCard from "@/components/FabricCard.vue";
import { GetCollection, GetCompany } from "@/typings/store";
import { CompanyResponse } from "@/typings/company";
import { formatImageUrl } from "@/utils/image";
import { CollectionDetailResponse } from "@/typings/collection";
import { useRouter } from "vue-router";
import { loading } from "@/utils";

const Component = defineComponent({
  name: "SupplierDetail",
  components: {
    FabricCard
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const { id } = toRefs(props);

    const detail = computed<CompanyResponse>(() => store.state.company.detail);
    const collectionDetail = computed<CollectionDetailResponse>(
      () => store.state.collection.detail
    );

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetCompany, id.value);
        const collectionId = detail.value.company_home_page.chosen_collection;
        if (collectionId) {
          await store.dispatch(GetCollection, collectionId);
        }
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onBrandClick = () => {
      window.open(detail.value.company_home_page.official_website, "blank");
    };

    return {
      detail,
      collectionDetail,

      onBrandClick,
      formatImageUrl
    };
  }
});

export default Component;
